/* App Container */
.app {
    padding: 2rem;
  }
  
  /* App Header */
  .app h1 {
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2rem;
    color: #2c3e50;
  }
  
  /* Optional: To give a card look */
  .app .task-form,
  .app .task-list {
    background: #fff;
    border-radius: 8px;
    padding: 1.5rem;
    margin-bottom: 2rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Responsive Layout */
  @media (max-width: 768px) {
    .app {
      padding: 1rem;
    }
  
    .app h1 {
      font-size: 1.5rem;
    }
  }
  .dashboard {
  text-align: center;
  background: linear-gradient(135deg, #74ebd5 0%, #ACB6E5 100%);
  min-height: 100vh;
  padding: 40px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.dashboard h1 {
margin-bottom: 30px;
font-size: 2.5rem;
color: #2c3e50; /* Dark color for better contrast */
font-weight: 700;
}